import "./card.scss"

export default function Card({ projects }) {
    console.log(projects)
    return (
        <>


            {projects &&
                projects.map((project) => (

                    <div key={project.title} className="card" id="card">
                        <img className="card-img-top" src="data:image/png;base64," alt="Card image cap" />
                        <div class="card-body">
                            <h5 className="card-title">{project.title}</h5>
                            <p className="card-text">{project.description}</p>
                            <a href={project.website} className="btn btn-primary">Go somewhere</a>
                        </div>

                    </div>
                ))
            }
        </>)
}
